import React from 'react';
import classes from './login.module.css';
import { Formik } from 'formik';
// import requiredField from '../utilities/validators';
import { Navigate } from 'react-router-dom';
import validationSchema from '../utilities/validators/validationSchema';
import { FormControls } from '../common/FormsControls/FormsControls';

/*
  initialValues -   
  validateOnBlur -         . 
  onSumbit={() => {}} - ,        
                              ,         ( )

   children,    ,   :
  values        - 
  errorts       - 
  touched       -       
  handleChange  -        
  handleBlur    -        - 
  isValid       -         
  handleSubmit  -           onSubmit()  
  dirty         -    -   
  
      - jsx
*/

const Login = ({ isAuth, userId, logIn, captcha }) => {
  if (isAuth) return <Navigate to={`/profile/${userId}`} />;

  return (
    <div>
      <h1>Login</h1>
      <Formik
        initialValues={{
          email: '',
          password: '',
          rememberMe: false,
          captcha: '',
        }}
        // validateOnBlur={true}  -  
        // validateOnChange={true} -  
        onSubmit={(values, { resetForm, setStatus, setSubmitting }) => {
          logIn(values, setStatus);
          resetForm();
          setSubmitting(false);
        }}
        validationSchema={validationSchema(30, 5)}
        // validate={requiredField}
      >
        {({
          values,
          errors,
          touched,
          handleChange,
          handleBlur,
          isValid,
          handleSubmit,
          dirty,
          status,
        }) => {
          return (
            <form>
              <div>
                <div>
                  <label htmlFor="email">Email</label>
                </div>
                <input
                  type="email"
                  id="email"
                  name="email"
                  onChange={handleChange}
                  onBlur={handleBlur}
                  value={values.email}
                  className={touched.email && errors.email && classes.error}
                ></input>
              </div>
              <FormControls touched={touched} errors={errors} value="email" />
              <div>
                <div>
                  <label htmlFor="password">Password</label>
                </div>
                <input
                  type="password"
                  id="password"
                  name="password"
                  onChange={handleChange}
                  onBlur={handleBlur}
                  value={values.password}
                  className={touched.password && errors.password && classes.error}
                ></input>
              </div>
              <FormControls touched={touched} errors={errors} value="password" />
              <div>
                <input
                  type="checkbox"
                  name="rememberMe"
                  value={values.rememberMe}
                  onChange={handleChange}
                ></input>{' '}
                Remember me
              </div>
              {captcha && (
                <div className={classes.captchaBlock}>
                  <div>
                    <input
                      type="text"
                      name="captcha"
                      value={values.captcha}
                      placeholder="captcha"
                      onChange={handleChange}
                      // onBlur={handleBlur}
                    ></input>
                  </div>
                  <div>
                    <img src={captcha} alt="captcha"></img>
                  </div>
                </div>
              )}
              <div>
                <button disabled={!isValid && !dirty} onClick={handleSubmit} type={'submit'}>
                  Log in
                </button>
              </div>
              <div>{status}</div>
            </form>
          );
        }}
      </Formik>
    </div>
  );
};

export default Login;
